    flex-grow: 1; }
    .kind-edit-wrapper .kind-edit input, .kind-edit-wrapper .kind-edit select, .kind-edit-wrapper .kind-edit textarea {
      display: block;
      width: 100%; }
    .kind-edit-wrapper .kind-edit textarea {
      height: 10em; }
    .kind-edit-wrapper .kind-edit input[type='checkbox'] {
      display: inline-block;
      width: auto;
      width: initial; }
    .kind-edit-wrapper .kind-edit input[type='number'] {
      width: 5em; }
    .kind-edit-wrapper .kind-edit .kind-settings .kind-options {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      margin-top: 1em; }
      .kind-edit-wrapper .kind-edit .kind-settings .kind-options label {
        flex-basis: calc( 50% - 1em);
        flex-grow: 0;
        display: block;
        margin: 0.5em; }
      .kind-edit-wrapper .kind-edit .kind-settings .kind-options input {
        margin-right: 0.5em; }
    .kind-edit-wrapper .kind-edit .field-form {
      border-style: solid;
      border-color: grey;
      background-color: #f9f9f9;
      border-radius: 3px;
      border-width: 1px;
      margin: 5px;
      padding: 5px;
      position: relative; }
      .kind-edit-wrapper .kind-edit .field-form .delete-field {
        position: absolute;
        right: 5px; }
      .kind-edit-wrapper .kind-edit .field-form > .field-section {
        width: 400px; }
      .kind-edit-wrapper .kind-edit .field-form .field-middle {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap; }
        .kind-edit-wrapper .kind-edit .field-form .field-middle .field-section {
          flex-basis: 400px; }
      .kind-edit-wrapper .kind-edit .field-form .field-type {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap; }
        .kind-edit-wrapper .kind-edit .field-form .field-type .field-type-group {
          display: flex;
          flex-direction: row;
          flex-wrap: nowrap;
          width: calc( 400px + 1em); }
          .kind-edit-wrapper .kind-edit .field-form .field-type .field-type-group .field-section {
            width: -webkit-fit-content;
            width: -moz-fit-content;
            width: fit-content; }
            .kind-edit-wrapper .kind-edit .field-form .field-type .field-type-group .field-section.factor-button {
              display: flex;
              flex-direction: column-reverse; }
          .kind-edit-wrapper .kind-edit .field-form .field-type .field-type-group .units {
            flex-shrink: 1;
            flex-grow: 1;
            width: auto; }
          .kind-edit-wrapper .kind-edit .field-form .field-type .field-type-group .dimension-labels .dimension-field {
            display: inline-block;
            margin: 0.5em;
            flex-basis: 33%;
            max-width: calc( ( 400px - 2em ) / 3); }
      .kind-edit-wrapper .kind-edit .field-form .field-checkboxes {
        margin: 0.5em; }
        .kind-edit-wrapper .kind-edit .field-form .field-checkboxes label {
          display: inline-block;
          margin: 0.5em; }
        .kind-edit-wrapper .kind-edit .field-form .field-checkboxes input {
          margin-left: 0.5em; }
    .kind-edit-wrapper .kind-edit .field-section {
      margin: 0.5em; }
    .kind-edit-wrapper .kind-edit .components-toolbar {
      display: block;
      position: absolute;
      left: -38px;
      border-color: rgba(66, 88, 99, 0.4);
      border-radius: 4px; }
      .kind-edit-wrapper .kind-edit .components-toolbar div {
        margin-left: 0; }
      .kind-edit-wrapper .kind-edit .components-toolbar button {
        width: 28px;
        height: 24px;
        padding: 0 !important;
        min-width: 24px !important; }
      .kind-edit-wrapper .kind-edit .components-toolbar .components-button:focus:before {
        box-shadow: none !important; }
      .kind-edit-wrapper .kind-edit .components-toolbar svg {
        width: 28px;
        height: 24px; }
  .kind-edit-wrapper .field-instructions {
    flex-basis: 30%;
    flex-grow: 0; }

.factor-edit-modal .factor-button {
